@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria{
    @include m.paddingSupEInf;
    text-align: center
}


.galeria-imagenes{
    color: v.$blanco;
    @include m.grid(2,1em);
    @include m.tablet{
        @include m.grid(4,1.5em);
    }

    @include m.pc{
        @include m.grid(6,2em);
    }    
    picture{
        &:hover{
            cursor: pointer;
        }
    }
}

.overlay{
    position: fixed;
    background: rgba(0, 0, 0, 0.80);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    width: 100%;
    max-height: 100%;
    @include m.flex(center,center,column);
    .panelImagen{
        z-index: 5;
        @include m.flex(center,center);
        picture{
            display: block;
            max-width: 70rem;

            @include m.mobileLadeado{
                max-width: 40rem;
            }
            @include m.pc{
                max-width: 70rem;
            }
        }
    }
}