@use "base/variables" as v;
@use 'base/mixins' as m;

.header{
    &.fijo{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        box-shadow: .1em .3em .2em v.$gris;
    }

    background: v.$colorCuaternario;
}

.contenido-header{
    @include m.flex(space-between,center);
    position: relative;

    h1{
        color: v.$blanco;
        text-align: center;
        margin: 0;
        padding:.1em 0;
    }

    .butonMenu{
        padding: 0;
        @include m.tablet{
            display: none;
        }

        &:active{
            background-color: darken(v.$colorCuaternario, 4%);
        }
    }
}

.navMenu{
    position: absolute;
    top: 100%;
    width: calc(100% + (100% - v.$anchoContainer));
    left: calc(0% - (100% - v.$anchoContainer)/2);
    z-index: 30;
    background-color: rgba(0, 0, 0, 0.90);
    visibility: hidden;

    @include m.tablet{
        visibility: visible;
        display: block;
        position: static;
        width: auto;
        background-color: transparent;
    }

    a{
        padding: 1em 0;
        display: block;
        text-align: center;
        color: v.$blanco;
        padding-bottom: 1em;

        @include m.tablet{
            display: inline;
            margin-left: 1em;
        }

        &:hover,&:active{
            color: v.$colorSecundario;
        }
    }
}

.visible{
    visibility: visible;
}