@use 'base/variables' as v;
//Media Queries
@mixin mobile{
    @media (min-width: v.$mobile){
        @content;
    }
}
@mixin mobileLadeado{
    @media (min-width: v.$mobileLadeado){
        @content;
    }
}
@mixin tablet{
    @media (min-width: v.$tablet){
        @content;
    }
}
@mixin pc{
    @media (min-width: v.$pc){
        @content;
    }
}
@mixin pcXL{
    @media (min-width: v.$pcXL){
        @content;
    }
}
@mixin fuentePorcentaje{
    @media (min-width: 450px){
        @content;
    }
}

//Layout

@mixin grid($columnas:1, $gap:0){
    display: grid;
    grid-template-columns: repeat($columnas,1fr);
    gap: $gap;
}

@mixin flex($justify:start,$alineacion:start,$direccion: row){
    display: flex;
    justify-content: $justify;
    align-items: $alineacion;
    flex-direction: $direccion;
}

@mixin paddingSupEInf{
    padding: 3em 0;
};