@use 'base/variables' as v;
@use 'base/mixins' as m;

.line-up{
    background-color: v.$colorSecundario;
    @include m.paddingSupEInf;

    h3,p{
        text-align: center;
    }

    .dia{
        text-transform: uppercase;
    }
}

.escenarios{

    @include m.grid(1,1em);

    @include m.tablet{
        @include m.grid(2,3em);
    }
}

.escenario{
    padding: 1em 0;
    .nombre-escenario{
        margin-bottom: 0;
    }
    .icon-tabler{
        display: block;
        margin: 2em auto;
    }
    .calendario{
        li{
            padding: 1em 1.5em;
            font-weight: bold;
            span{
                font-weight: 400;
            }
        }
    }
}

.bg-estilo1{
    background-color: v.$colorCuaternario;

    li:nth-child(odd){
        background-color: darken(v.$colorCuaternario,10%);
    }
}

.bg-estilo2{
    background-color: v.$colorQuinto;

    li:nth-child(odd){
        background-color: darken(v.$colorQuinto,10%);
    }
}