@use "variables" as v;
@use 'mixins' as m;

html{
    font-size:62.5% ;
    box-sizing: border-box;
    scroll-behavior: smooth;
}

*, *:before, *:after {
    box-sizing: inherit;
}

body{
    background-color: v.$colorPrincipal;

    &.fijarBody{
        overflow: hidden;
    }

    &.body-scroll{
        
        scroll-padding-top: 3.78rem;
        @include m.tablet{
            scroll-padding-top: 5.4rem;
        }
    }
}

p,a,li{
    color: v.$blanco;
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
    @include m.fuentePorcentaje{
        font-size: min(3.5vw, 2.2rem);
    }
}

a{
    text-decoration: none;
}

.container{
    max-width: 120rem;
    width: v.$anchoContainer;
    margin: 0 auto;
}

h1,h2,h3{
    font-family: v.$fuenteSecundaria;
    margin-top: 0;
    color: v.$blanco;
}
h1{
    font-family: v.$fuenteSecundaria;
    font-size: 2.8rem;
    @include m.tablet{
        font-size: 4rem;
    }
}
h2{
    font-size: 2.4rem;
    @include m.fuentePorcentaje{
        font-size: min(5.33vw,3.8rem);
    }
}
h3{
    font-size: 1.9rem;
    @include m.fuentePorcentaje{
        font-size: min(4.22vw,3.2rem);
    }
}

img{
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}

ul{
    list-style: none;
    padding: 0;
    margin: 0;
}

.boton{
    color: v.$blanco;
    border-radius: 50%;
    font-size: 2.8rem;
    
    margin: 0;
    &:hover{
        cursor: pointer;
    }

    padding: .5em;

    @include m.tablet{
        font-size: 3rem;
        padding: 1em;
    }

    @include m.pc{
        font-size: 4rem;
    }
}