@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-festival{
    @include m.grid(1,2em);

    @include m.tablet{
        @include m.grid(2,2em);
    }
    @include m.paddingSupEInf;
}

.contenido-festival{
    @include m.flex(center,auto,column);
    h2{
        color: v.$colorSecundario;    
    }

    .fecha{
        color: v.$blanco;
        font-weight: bold;
    }
}