@use 'base/variables' as v;
@use 'base/mixins' as m;

.boletos{

    background-color: v.$colorSecundario;
    @include m.paddingSupEInf;

    h3{
        text-align: center;
        margin-bottom: 0;
    }

    @include m.grid(1,1em);

    .contenedor-boletos{
        @include m.tablet{
            @include m.grid(2,2em);
        }

        @include m.pc{
            @include m.grid(2,3em);
        }
    }
}

.pase{

    p{
        text-align: center;
    }
    &-header{
        background-color: v.$colorCuaternario;
        padding: 1em 0;
    }

    &-body{
        background-color: v.$blanco;
        ul{
            margin: 0;
            li{
                padding: .5em 1em;
                color: v.$negro;
                border-bottom: .05em solid v.$gris;
            }

            li:last-of-type{
                border: 0;
            }
        }
    }

    &-footer{
        background-color: v.$colorQuinto;
        padding: 1em 0;
    }

    &-footer,&-header,&-body{
        p{
            margin:0;
            text-align: center;
            text-transform: uppercase;
        }
    }
}