html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #140626;
}
body.fijarBody {
  overflow: hidden;
}
body.body-scroll {
  scroll-padding-top: 3.78rem;
}
@media (min-width: 768px) {
  body.body-scroll {
    scroll-padding-top: 5.4rem;
  }
}

p, a, li {
  color: #fff;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 450px) {
  p, a, li {
    font-size: min(3.5vw, 2.2rem);
  }
}

a {
  text-decoration: none;
}

.container {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
}

h1, h2, h3 {
  font-family: "Tilt Neon", sans-serif;
  margin-top: 0;
  color: #fff;
}

h1 {
  font-family: "Tilt Neon", sans-serif;
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4rem;
  }
}

h2 {
  font-size: 2.4rem;
}
@media (min-width: 450px) {
  h2 {
    font-size: min(5.33vw, 3.8rem);
  }
}

h3 {
  font-size: 1.9rem;
}
@media (min-width: 450px) {
  h3 {
    font-size: min(4.22vw, 3.2rem);
  }
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.boton {
  color: #fff;
  border-radius: 50%;
  font-size: 2.8rem;
  margin: 0;
  padding: 0.5em;
}
.boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .boton {
    font-size: 3rem;
    padding: 1em;
  }
}
@media (min-width: 1024px) {
  .boton {
    font-size: 4rem;
  }
}

.header {
  background: #240940;
}
.header.fijo {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-shadow: 0.1em 0.3em 0.2em #8b8a92;
}

.contenido-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  position: relative;
}
.contenido-header h1 {
  color: #fff;
  text-align: center;
  margin: 0;
  padding: 0.1em 0;
}
.contenido-header .butonMenu {
  padding: 0;
}
@media (min-width: 768px) {
  .contenido-header .butonMenu {
    display: none;
  }
}
.contenido-header .butonMenu:active {
  background-color: #1a062e;
}

.navMenu {
  position: absolute;
  top: 100%;
  width: 110%;
  left: -5%;
  z-index: 30;
  background-color: rgba(0, 0, 0, 0.9);
  visibility: hidden;
}
@media (min-width: 768px) {
  .navMenu {
    visibility: visible;
    display: block;
    position: static;
    width: auto;
    background-color: transparent;
  }
}
.navMenu a {
  padding: 1em 0;
  display: block;
  text-align: center;
  color: #fff;
  padding-bottom: 1em;
}
@media (min-width: 768px) {
  .navMenu a {
    display: inline;
    margin-left: 1em;
  }
}
.navMenu a:hover, .navMenu a:active {
  color: #F23D4C;
}

.visible {
  visibility: visible;
}

.hero {
  position: relative;
  max-height: 60rem;
  overflow: hidden;
}
.hero .overlay {
  background: rgba(0, 0, 0, 0.5);
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
}
.hero video {
  display: block;
  width: 100%;
}

.contenido-hero {
  z-index: 1;
}
.contenido-hero h2 {
  color: #fff;
  font-size: 2.8rem;
  margin: 0;
}
@media (min-width: 450px) {
  .contenido-hero h2 {
    font-size: min(6.25vw, 8rem);
  }
}
.contenido-hero p {
  background-color: #F23D4C;
  color: #fff;
  font-weight: bold;
  padding: 0.5em;
  margin: 0.5em 0;
  width: fit-content;
  border-radius: 0.2em;
}

.sobre-festival {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2em;
  padding: 3em 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2em;
  }
}

.contenido-festival {
  display: flex;
  justify-content: center;
  align-items: auto;
  flex-direction: column;
}
.contenido-festival h2 {
  color: #F23D4C;
}
.contenido-festival .fecha {
  color: #fff;
  font-weight: bold;
}

.line-up {
  background-color: #F23D4C;
  padding: 3em 0;
}
.line-up h3, .line-up p {
  text-align: center;
}
.line-up .dia {
  text-transform: uppercase;
}

.escenarios {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
}
@media (min-width: 768px) {
  .escenarios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3em;
  }
}

.escenario {
  padding: 1em 0;
}
.escenario .nombre-escenario {
  margin-bottom: 0;
}
.escenario .icon-tabler {
  display: block;
  margin: 2em auto;
}
.escenario .calendario li {
  padding: 1em 1.5em;
  font-weight: bold;
}
.escenario .calendario li span {
  font-weight: 400;
}

.bg-estilo1 {
  background-color: #240940;
}
.bg-estilo1 li:nth-child(odd) {
  background-color: #0b0313;
}

.bg-estilo2 {
  background-color: #2E038C;
}
.bg-estilo2 li:nth-child(odd) {
  background-color: #1e025a;
}

.galeria {
  padding: 3em 0;
  text-align: center;
}

.galeria-imagenes {
  color: #fff;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1em;
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.5em;
  }
}
@media (min-width: 1024px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2em;
  }
}
.galeria-imagenes picture:hover {
  cursor: pointer;
}

.overlay {
  position: fixed;
  background: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  width: 100%;
  max-height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.overlay .panelImagen {
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.overlay .panelImagen picture {
  display: block;
  max-width: 70rem;
}
@media (min-width: 500px) {
  .overlay .panelImagen picture {
    max-width: 40rem;
  }
}
@media (min-width: 1024px) {
  .overlay .panelImagen picture {
    max-width: 70rem;
  }
}

.boletos {
  background-color: #F23D4C;
  padding: 3em 0;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 1em;
}
.boletos h3 {
  text-align: center;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2em;
  }
}
@media (min-width: 1024px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3em;
  }
}

.pase p {
  text-align: center;
}
.pase-header {
  background-color: #240940;
  padding: 1em 0;
}
.pase-body {
  background-color: #fff;
}
.pase-body ul {
  margin: 0;
}
.pase-body ul li {
  padding: 0.5em 1em;
  color: #000;
  border-bottom: 0.05em solid #8b8a92;
}
.pase-body ul li:last-of-type {
  border: 0;
}
.pase-footer {
  background-color: #2E038C;
  padding: 1em 0;
}
.pase-footer p, .pase-header p, .pase-body p {
  margin: 0;
  text-align: center;
  text-transform: uppercase;
}

.footer {
  background-color: #240940;
  text-align: center;
  padding: 2em 0;
}
.footer p {
  margin: 0;
}