@use 'base/variables' as v;
@use 'base/mixins' as m;

.hero{
    position: relative;
    max-height: 60rem;
    overflow: hidden;
    
    .overlay{
        background: rgba(0, 0, 0, 0.5);
        position: absolute;
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
    }
    video{
        display: block;
        width: 100%;
    }
}

.contenido-hero{
    z-index: 1;
    h2{
        color: v.$blanco;
        font-size: 2.8rem;
        margin: 0;

        @include m.fuentePorcentaje{
            font-size: min(6.25vw, 8rem);
        }
    }

    p{
        background-color: v.$colorSecundario;
        color: v.$blanco;
        font-weight: bold;
        padding: .5em;
        margin: .5em 0;
        width: fit-content;
        border-radius: .2em;
    }
}